.cardcontainer{
    display:flex;
    flex-direction: column;
    border:2px solid gray;
    border-radius: 10px;
    cursor:pointer;
    background-color:rgb(192, 225, 235);
    -webkit-transition: all .25s ease-in;
    transition: all .25s ease-in;
    margin:10px;
}

.cardcontainer:hover{
    -webkit-transform:scale(1.05);
            transform:scale(1.05);
}